body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* App */
.App {
    display: flex;
}

/* SearchContainer */
.container-search {
    width: 30%;
    padding: 30px;
    border: 1px solid black;
    margin: 10px;
}

.container-logo {
    width: 100%;
    text-align: center;
}

.logo {
    width: 50%;
}

.search {
    width: 100%;
    padding: 30px 10px;
}

select {
    margin: 0 0 10px 0;
}

.container-range {
    margin: 0 0 10px 0;
    display: flex;
    flex-direction: column;
}

/* MapContainer */
.container-map {
    width: 100vw;
    height: 100vh;
}

.map-text {
    font-size: 14px;
}

/* Ribbon */
.container-ribbon {
    color: #000;
    background-color: rgba(255, 255, 255, 0.655);
    z-index: 3;
}

@media only screen and (max-width: 768px) {
    .App {
        flex-direction: column;
    }

    .container-search {
        width: 100%;
        border:  none;
        margin: 0;
    }
}
